.Behavior {
	margin: 2.5rem 2rem 0;

	&__Form {
		padding: 39px 60px;
	}

	&__InputGroup {
		display: flex;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}

	&__InputWrapper {
		position: relative;
		display: flex;
		align-items: center;

		& > input {
			padding-left: 1.25rem;
		}
	}

	&__Icon {
		position: absolute;
		left: 10px;
		top: 10px;
	}

	&__Column {
		display: flex;
		flex-direction: column;
	}

	&__Row {
		display: flex;
	}

	&__WithBottom {
		margin-bottom: 1rem;

		&:nth-child(2) {
			margin-bottom: 24px;
		}
	}

	&__Label {
		margin-right: 1rem;
		display: inline-block;
		width: 7.75rem;
		text-align: right;

		font-style: normal;
		font-weight: 400;
		font-size: 1rem;
		line-height: 1.5rem;

		color: var(--text);

	}

	&__InputGroup:nth-child(3) &__Label {
		align-self: flex-start;
	}

	&__SelectWrapper {
		position: relative;

		&--currency {
			& > select {
				width: 5rem;
			}
		}
	}

	&__SelectArrow {
		position: absolute;
		top: 0.5rem;
		right: 0.625rem;
	}

	&__Select {
		padding: 0.4rem 1.625rem 0.5rem 0.7rem;
		height: 2.5rem;
		border: 1px solid var(--border);
		border-radius: 0.375rem;

		font-size: 1rem;
		line-height: 1.5rem;

		color: var(--text);
		background: #fff;

		-webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
	}

	&__Wide {
		width: 18.5rem;
	}

	&__Amount {
		width: 12rem;
		margin-right: 24px;
		padding: 8px 12px;
		height: 40px;
		border: 1px solid var(--border);
		border-radius: 6px;

		font-size: 1rem;
		line-height: 1.5rem;

		color: var(--text);
		box-sizing: border-box;
	}

	&__RadioLabel, &__CheckboxLabel {
		font-family: 'Work Sans', sans-serif;
		font-style: normal;
		font-weight: 400;
		font-size: 1rem;
		line-height: 1.5rem;

		color: var(--text);
	}

	&__CheckboxLabel {
		margin-bottom: 8px;
	}

	&__SliderWrapper {
		margin-left: -4px;
		position: relative;
		display: flex;
		align-items: center;

		& > label {
			margin-left: 1rem;

			font-family: 'Work Sans', sans-serif;
			font-style: normal;
			font-weight: 400;
			font-size: 1rem;
			line-height: 1.5rem;

			color:var(--text);
		}

		&__InputRange {
			width: 218px;
		}
	}

	@media only screen and (max-width: 990px) {
		&__InputGroup {
			margin-left: 2rem;
			flex-direction: column;
			align-items: flex-start;
		}

		&__Label {
			margin-top: 0.5rem;
			margin-bottom: 0.5rem;
			text-align: left;
		}

		&__Form {
			padding: 1rem;
		}
	}

	@media only screen and (max-width: 748px) {
		&__InputGroup {
			margin-left: 1rem;
		}

		&__InputWrapper {
			width: 100%;
		}

		&__Amount {
			width: 100%;
			margin-right: 0;
			margin-bottom: 16px;
		}

		&__SelectWrapper {
			width: 100%;

			&--currency {
				display: flex;
				justify-content: flex-end;
			}
		}

		&__Wide {
			width: 100%;
		}

		&__Goal {
			width: 100%;
			flex-direction: column;
		}
	}
}